import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { useForm } from '../../hooks/useForm';
import {
  Input,
  EmailInput,
  PasswordInput,
  Button
} from '@ya.praktikum/react-developer-burger-ui-components';
import { fetchNewUserRegistration, RESET_FAILED } from '../../services/actions/user';
import Preloader from '../../components/preloader/preloader';
import styles from './register.module.css';


function Register() {
  const dispatch = useDispatch();

  const { values, handleChange } = useForm({
    name: '',
    email: '',
    password: ''
  });

  const isRequest = useSelector((store) => store.userReducer.isRequest);
  const isFailed = useSelector((store) => store.userReducer.isFailed);

  useEffect(() => {
    resetState();
  }, []);

  const resetState = () => {
    dispatch({
      type: RESET_FAILED
    });
  }

  const onSubmit = (evt) => {
    evt.preventDefault();

    dispatch(fetchNewUserRegistration(values.name, values.email, values.password));
  }

  return (
    <main className='centeredContainer'>
      {isRequest && (
        <Preloader />
      )}

      {!isRequest && !isFailed && (
        <>
          <form
            className={styles.form}
            onSubmit={onSubmit}
          >
            <h1 className='text text_type_main-medium'></h1>

            <Input
              value={values.name}
              name='name'
              placeholder=''
              type='text'
              onChange={handleChange}
            />

            <EmailInput
              value={values.email}
              name='email'
              placeholder='E-mail'
              isIcon={false}
              errorText='  e-mail'
              onChange={handleChange}
            />

            <PasswordInput
              value={values.password}
              name='password'
              errorText='    6 '
              onChange={handleChange}
            />

            <Button
              htmlType='submit'
              type='primary'
              size='medium'
              disabled={!values.name || !values.email || values.password.length < 6 ? true : false}
            >
              
            </Button>
          </form>

          <div className={styles.text}>
            <p className='text text_type_main-default text_color_inactive'>
               ? <Link className={styles.link} to='/login'></Link>
            </p>
          </div>
        </>
      )}

      {isFailed && !isRequest && (
        <>
          <h2 className='text text_type_main-large text_color_inactive'>
             
          </h2>

          <Button
            htmlType='button'
            type='primary'
            size='medium'
            onClick={resetState}
          >
             
          </Button>

          <div className={styles.text}>
            <p className='text text_type_main-default text_color_inactive'>
                  ? <Link className={styles.link} to='/login'></Link>
            </p>
          </div>
        </>
      )}
    </main>
  );
}

export default Register;
